import React from 'react';
import codeImport from './imgs/Githubresp.png';


const Skills = () => {
    
    return(
        <>
        
        <section className="skills section" id="skills">

         <h2 className="section-title">Skills</h2>

            <div className="skills__container bd-grid">
                <div>
                <h2 class="skills__subtitle">Profesional Skills</h2>
         <p className="skills__text">I have a passion for <b> Computer Science</b> and <b>Programming languages</b> . I have had <b>experience</b>  with langauages to include: <b>Swift</b> , <b>Javascript</b> , and <b>Python</b> . 
         Throughout my time in college I have spent a significant amount of time programming, and gettign a <b>Deeper </b> understanding for Software.
         I plan to progress my skills daily, while <b>learning</b>  more and more everyday. I am not just addicted to Programming, but Learning.
         I have over a years worth of <b>Github Projects</b> , and over 20 in <b>React.js</b> . I wish to really use my Vanilla Javascript skills to become a better <b>React Developer</b> .
         </p>
         <div className="skills__data">
            <div className="skills__names">
            <i className='bx bxl-html5 skills__icon'></i>
            <span className="skills__name">JavaScript</span>
            </div>
            <div className="skills__bar skills__html">

              </div>
              <div>
                    <span className="skills__percentage">95%</span>
                 </div>

         </div>
         <div className="skills__data">
            <div className="skills__name">
            <i className='bx bxl-css3 skills__icon'></i>
            <span className="skills__name">CSS</span>
            </div>
            <div className="skills__bar skills__css">
                                
             </div>
             <div>
          <span className="skills__percentage">85%</span>
            </div>
         </div>
         <div className="skills__data">
            <div className="skill__names">
            <i className='bx bxl-javascript skills__icon' ></i>
             <span clasName="skills__name">React</span>

            </div>
            <div className="skills__bar skills__js">
                                
              </div>
              <div>
                <span className="skills__percentage">65%</span>
                </div>

         </div>
           <div className="skills__data">
             <div className="skills__names">
             <i className='bx bxs-paint skills__icon'></i>
               <span className="skills__name">UX/UI</span>
             </div>
             <div className="skills__bar skills__ux">
                                
             </div>
             <div>
             <span class="skills__percentage">85%</span>
             </div>

           </div>
             
        </div>
        <div className="github-img-img">
            <a href="https://github.com/Software-with-Alex">
            <img  src={codeImport} alt=""/>
            </a>
               

        </div>
                
</div>
        
        
        </section>
        
        
        </>
    )
}


export default Skills;